import style from "./Explore.module.css";
import { usePostsContext } from "contexts";
import { useEffect, useRef, useState } from "react";
import { PostLayoutItem } from "components";

const Explore = () => {
  const {
    postState: { postsAll, postAllLoading },
    getAllPosts,
  } = usePostsContext();
  const arr3 = useRef([]);
  const [arrPosts, setArrPosts] = useState();
  useEffect(() => {
    if (postAllLoading) {
      getAllPosts();
    }
  }, []);
  const them = useRef(false);
  const toggleThem = () => {
    them.current = !them.current;
  };
  useEffect(() => {
    const handleRenderPosts = () => {
      if (postsAll.length !== 3) {
        for (let i = 0; i < postsAll.length / 3 - 1; i++) {
          arr3.current.push([
            postsAll[(i + 1) * 3 - 3],
            postsAll[(i + 1) * 3 - 2],
            postsAll[(i + 1) * 3 - 1],
          ]);
        }
      } else {
        arr3.current.push([postsAll[0], postsAll[1], postsAll[2]]);
      }
      if (postsAll.length % 3 === 1) {
        arr3.current.push([postsAll[postsAll.length - 1]]);
      }
      if (postsAll.length % 3 === 2) {
        arr3.current.push([
          postsAll[postsAll.length - 2],
          postsAll[postsAll.length - 1],
        ]);
      }
      setArrPosts(arr3.current);
      arr3.current = [];
    };
    handleRenderPosts();
  }, [postsAll]);
  console.log(arrPosts);
  return (
    <>
      <div className={style.root}>
        <div className="grid wide">
          <div className="row">
            <div className="col l12">
              {arrPosts?.map((p, i) => {
                return (
                  <PostLayoutItem
                    key={i}
                    data={p}
                    index={i}
                    last={i === arrPosts.length - 1}
                    them={them}
                    toggleThem={toggleThem}
                  />
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Explore;
