import "./authForm.css";
import { Link } from "react-router-dom";
import { useState, useContext, useRef, useEffect } from "react";
import { AuthContext } from "../../contexts/AuthContext";
import Alert from "../alert/Alert";
const Login = () => {
  const { login } = useContext(AuthContext);
  const [alert, setAlert] = useState(null);

  const [value, setValue] = useState({
    username: "",
    password: "",
  });
  const myRef = useRef();
  useEffect(() => {
    const handleClick = (e) => {
      if (e.keyCode === 13) {
        myRef.current.click();
      }
    };
    document.addEventListener("keyup", handleClick);
    return () => {
      document.removeEventListener("keyup", handleClick);
    };
  }, []);
  const { username, password } = value;
  const handleChange = (e) => {
    setValue({
      ...value,
      [e.target.name]: e.target.value,
    });
  };
  const handleLogin = async (value) => {
    const loginData = await login(value);
    if (!loginData.success) {
      setAlert(loginData.message);
    }
  };
  return (
    <>
      <div className="wrap-form">
        <div className="auth-form">
          <div className="logo-form"></div>
          <input
            type="text"
            name="username"
            className="input-form"
            placeholder="Tn ti khon"
            onChange={handleChange}
            value={username}
          />
          <input
            type="password"
            className="input-form"
            name="password"
            placeholder="Mt khu"
            onChange={handleChange}
            value={password}
          />
          <div>
            {alert && <Alert info={alert} />}
            {/* add class disabled to disabled */}
            <button
              className="form-button"
              ref={myRef}
              onClick={() => handleLogin(value)}
            >
              ng nhp
            </button>
            <div className="wrap-switch">
              <p className="switch-text">Bn cha c ti khon ?</p>
              <Link to="/register" className="switch-link">
                ng k
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
