import "./profile.css";
import { useContext, useRef, useEffect, useState, memo } from "react";
import { AuthContext } from "../../contexts/AuthContext";
import { apiUrl } from "../../contexts/constants";
import ItemImg from "../itemImg/ItemImg";
import Spinner from "../spinner/Spinner";
import { useHistory } from "react-router-dom";
import { usePostsContext, RELOAD_POST } from "contexts";
import { ModalPostDetail } from "components";

const Profile = ({ username }) => {
  const history = useHistory();
  const {
    authState: { user },
    updatedAvt,
    getUserFr,
    addFriend,
    deleteFriend,
    addFollow,
    deleteFollow,
  } = useContext(AuthContext);
  const { dispatch, getPostById } = usePostsContext();

  const imgRef = useRef();
  const inputRef = useRef();
  const [userReplace, setUserReplace] = useState({
    user,
    profileLoading: false,
    isFriend: false,
    following: user.friends.length,
    numberFollowers: user.followers.length,
    posts: null,
  });

  const { profileLoading } = userReplace;
  const handleAddFriend = async () => {
    await addFollow(username);
    addFriend(userReplace.user._id);
    dispatch({ type: RELOAD_POST });
  };
  const handleDeleteFriend = async () => {
    await deleteFollow(username);
    deleteFriend(userReplace.user._id);
    dispatch({ type: RELOAD_POST });
  };
  const handleMessage = () => {
    history.push(`/inbox/${userReplace.user._id}`);
  };
  useEffect(() => {
    const getUser = async () => {
      if (user.username !== username) {
        const res = await getUserFr(username);
        const posts = await getPostById(res.user._id);
        if (res.success) {
          if (user.friends.includes(res.user._id)) {
            setUserReplace({
              ...userReplace,
              user: res.user,
              profileLoading: true,
              isFriend: true,
              following: res.user.friends.length,
              numberFollowers: res.user.followers.length,
              posts: posts.posts,
            });
          } else {
            setUserReplace({
              ...userReplace,
              user: res.user,
              profileLoading: true,
              following: res.user.friends.length,
              isFriend: false,
              numberFollowers: res.user.followers.length,
              posts: posts.posts,
            });
          }
        }
      } else {
        const posts = await getPostById(user._id);
        setUserReplace({
          user: user,
          profileLoading: true,
          following: user.friends.length,
          isFriend: false,
          numberFollowers: user.followers.length,
          posts: posts.posts,
        });
      }
    };
    getUser();
    // eslint-disable-next-line
  }, [getUserFr, username, user]);
  const handlePreviewAvt = (e) => {
    const file = e.target.files[0];
    const img = URL.createObjectURL(file);
    URL.revokeObjectURL(file);
    imgRef.current.src = img;

    setUploadAvt(true);
  };
  const [n, setN] = useState(0);
  const [uploadAvt, setUploadAvt] = useState(false);
  const handleIncrease = () => {
    setN((prev) => {
      if (prev === userReplace?.posts.length - 1) {
        return prev;
      }
      return prev + 1;
    });
  };
  const handleCountDown = () => {
    setN((prev) => {
      if (prev === 0) {
        return prev;
      }
      return prev - 1;
    });
  };

  const handleUploadAvt = async (e) => {
    e.stopPropagation();
    await updatedAvt(inputRef.current.files[0], user);
    document.location.reload();
  };
  const [modal, setModal] = useState(false);

  const body = (
    <>
      {modal && <ModalPostDetail setModal={setModal} data={modal} />}
      <div className="profile-container">
        <div className="grid wide">
          <div className="row">
            <div className="col l8 lo2 d-flex">
              <label htmlFor="uploadAvt" className="profile-img">
                <img
                  src={`${apiUrl}/uploadfile/${userReplace.user.avt}`}
                  alt=""
                  ref={imgRef}
                />
                {user.username === username && (
                  <input
                    type="file"
                    name=""
                    hidden
                    id="uploadAvt"
                    onChange={handlePreviewAvt}
                    ref={inputRef}
                  />
                )}

                {uploadAvt && (
                  <button
                    onClick={handleUploadAvt}
                    className="uploadAvt-btn o5active"
                  >
                    Xong
                  </button>
                )}
              </label>
              <div className="profile-info">
                <div>
                  <h1 className="profile-info-username">
                    {userReplace.user.username}
                  </h1>
                  <button className="btn-edit-profile">
                    {username === user.username ? (
                      <span className="btn-edit-my-profile">
                        Chnh sa trang c nhn ( chc nng ang pht trin )
                      </span>
                    ) : userReplace.isFriend ? (
                      <span
                        onClick={handleDeleteFriend}
                        className="btn-edit-fr-profile"
                      >
                        B theo di
                      </span>
                    ) : (
                      <span
                        className="btn-edit-fr-profile"
                        onClick={handleAddFriend}
                      >
                        Theo di
                      </span>
                    )}
                  </button>
                  {user.username !== username && (
                    <button
                      className="btn-edit-profile-mesages"
                      onClick={handleMessage}
                    >
                      Nhn tin
                    </button>
                  )}
                </div>
                <div>
                  <span>{userReplace.posts?.length} bi vit</span>
                  <span>{userReplace.numberFollowers} ngi theo di</span>
                  <span>ang theo di {userReplace.following} ngi dng</span>
                </div>
                <h1 className="profile-info-fullName">
                  {userReplace.user.fullName}
                </h1>
              </div>
            </div>
          </div>
          <div className="slide-img">
            <div className="row">
              <div className="col l8 lo2">
                {userReplace.posts && userReplace.posts.length > 0 ? (
                  <ul className="list-img">
                    <ItemImg
                      position={n}
                      data={userReplace?.posts}
                      setModal={setModal}
                    />
                    <button className="btn-next-slide" onClick={handleIncrease}>
                      <i className="fas fa-arrow-circle-right"></i>
                    </button>
                    <button
                      className="btn-prev-slide"
                      onClick={handleCountDown}
                    >
                      <i className="fas fa-arrow-circle-left"></i>
                    </button>
                  </ul>
                ) : (
                  <h1 class="profile-title-noPosts">
                    Bn cha c bi vit no
                  </h1>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
  return profileLoading ? body : <Spinner />;
};

export default memo(Profile);
