import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import HomeContextProvider from "./contexts/HomeContext";
import AuthContextProvider from "./contexts/AuthContext";
import Auth from "./view/auth/Auth";
import Page from "./view/page/Page";
import ProtectedRoute from "./components/routing/ProtectedRoute";
import { ModalCreatePost } from "./components/modals/Modal";
import PostsContextProvider from "./contexts/PostsContext";

function App() {
  return (
    <AuthContextProvider>
      <PostsContextProvider>
        <Router>
          <Switch>
            <Route exact path="/login" render={() => <Auth route="login" />} />
            <Route
              exact
              path="/register"
              render={() => <Auth route="register" />}
            />
            <HomeContextProvider>
              <ProtectedRoute exact path="/" route="home" component={Page} />
              <ProtectedRoute
                exact
                path="/inbox"
                route="inbox"
                component={Page}
              />
              <ProtectedRoute
                exact
                path="/inbox/:id"
                route="inbox"
                component={Page}
              />
              <ProtectedRoute
                exact
                path="/explore"
                route="explore"
                component={Page}
              />
              <ProtectedRoute exact path="/profile/:user" component={Page} />
              <ProtectedRoute
                exact
                path="/create"
                component={ModalCreatePost}
              />
              <ProtectedRoute
                exact
                path="/nofification"
                route="nofification"
                component={Page}
              />
            </HomeContextProvider>
          </Switch>
        </Router>
      </PostsContextProvider>
    </AuthContextProvider>
  );
}

export default App;
