import "./inbox.css";
import { useRef, useEffect, useState, useContext } from "react";
import ConversationsItem from "../conversationsItem/ConversationsItem";
import Messages from "../messages/Messages";
import TextareaAutosize from "react-textarea-autosize";
import axios from "axios";
import { apiUrl } from "../../contexts/constants";
import { AuthContext } from "../../contexts/AuthContext";
import { io } from "socket.io-client";

import { ModalCreateMessage } from "../modals/Modal";
import { Link } from "react-router-dom";
import { useHomeContext } from "contexts";

const Inbox = ({ id }) => {
  const conversationIdRef = useRef();
  const chatConcentMessages = useRef();
  const buttonRef = useRef();
  const formMessagesInputRef = useRef();
  const listFrInConversations = useRef([]);

  //useContex
  const {
    authState: { user },
  } = useContext(AuthContext);
  const { socket } = useHomeContext();

  //useState
  const [conversations, setConversations] = useState(null);
  const [currentChat, setCurrentChat] = useState([]);
  const [infoFriend, setInfoFriend] = useState();
  const [idConversationCurrent, setIdConversationCurrent] = useState();
  const [message, setMessage] = useState();
  const [arrivalMess, setArrivalMess] = useState();
  const [conversationCurrent, setConversationCurrent] = useState();
  const [modalCreate, setModalCreate] = useState(false);
  const [actionConversation, setActionConversation] = useState(false);
  const getConversations = async () => {
    const res = await axios.get(
      `${apiUrl}/messenger/conversations/${user._id}`
    );
    if (res.data.success) {
      res.data.conversation.forEach((c) =>
        listFrInConversations.current.push(
          c.members[0] === user._id ? c.members[1] : c.members[0]
        )
      );
    }
    setConversations(res.data.conversation);
  };
  //useEffect
  useEffect(() => {
    socket.current.emit("addUser", user?._id);
    socket.current.on("arrivalMessage", async ({ messages }) => {
      console.log("co chay k ");
      setArrivalMess(messages);
    });
  }, []);
  useEffect(() => {
    if (arrivalMess) {
      if (idConversationCurrent === arrivalMess.conversationId) {
        setCurrentChat([...currentChat, arrivalMess]);
      } else {
        getConversations();
      }
    }
  }, [arrivalMess]);

  useEffect(() => {
    getConversations();
  }, [actionConversation]);

  useEffect(() => {
    if (id && conversations) {
      handleCreateMessage(id);
    }
  }, [conversations]);

  useEffect(() => {
    const handleKeyUp = (e) => {
      if (e.keyCode === 13) {
        e.preventDefault();
        buttonRef.current.click();
      }
    };
    const inputText = formMessagesInputRef.current;
    inputText?.addEventListener("keydown", handleKeyUp);
    return () => {
      inputText?.removeEventListener("keydown", handleKeyUp);
    };
  }, [currentChat]);

  // useEffect(() => {
  //   socket.current.emit("addUser", user._id);
  // }, [conversationCurrent]);

  // callback

  const handleSendMessage = async () => {
    const res = await axios.post(`${apiUrl}/messenger/messages`, {
      senderId: user._id,
      text: message,
      conversationId: conversationIdRef.current,
    });
    await axios.post(`${apiUrl}/messenger/addnew`, {
      conversationId: idConversationCurrent,
      idNew: infoFriend._id,
    });
    await socket.current.emit("sendMessage", {
      recevierId: conversationCurrent.find((userId) => userId !== user._id),

      messages: res.data.messages,
    });

    setCurrentChat([...currentChat, res.data.messages]);
    setMessage("");
    formMessagesInputRef.current.focus();
  };

  const hanleClickConversation = async (conversation) => {
    const res = await axios.get(
      `${apiUrl}/messenger/messages/${conversation._id}`
    );
    if (conversation.new) {
      await axios.delete(`${apiUrl}/messenger/removeNew/${conversation._id}`);
      getConversations();
    }

    setConversationCurrent(conversation?.members);
    setIdConversationCurrent(conversation._id);
    conversationIdRef.current = conversation?._id;
    const friendId = conversation?.members.find((m) => m !== user._id);
    const friendInfo = await axios.get(`${apiUrl}/user/?id=${friendId}`);
    setInfoFriend(friendInfo.data.user);
    setCurrentChat(res.data.messages);
  };

  const handleChangeText = (e) => {
    setMessage(e.target.value);
  };

  const handleCreateMessage = async (friendId) => {
    if (listFrInConversations.current.includes(friendId)) {
      setModalCreate(false);
      const currentConversation = await conversations.find((c) =>
        c.members.includes(friendId)
      );
      hanleClickConversation(currentConversation);
    } else {
      const res = await axios.post(`${apiUrl}/messenger/conversation/`, {
        member1: friendId,
        member2: user._id,
        new: "",
      });
      if (res.data.success) {
        setModalCreate(false);
        listFrInConversations.current.push(
          res.data.conversation.members[0] === user._id
            ? res.data.conversation.members[1]
            : res.data.conversation.members[0]
        );
        setConversations([...conversations, res.data.conversation]);
        hanleClickConversation(res.data.conversation);
      }
    }
  };
  return (
    <>
      <div className="section-inbox-wrap">
        <div className="grid wide">
          <div className="row">
            <div className="col l12">
              <div className="section-inbox">
                <div className="conversations">
                  <div className="conversations-header">
                    <h2 className="conversations-header-text">Tin nhn</h2>
                    <div
                      className="conversations-header-icon o5active"
                      onClick={() => setModalCreate(true)}
                    >
                      <i className="fas fa-plus-circle"></i>
                    </div>
                  </div>
                  <ul className="conversations-list">
                    {conversations &&
                      conversations.map((c, i) => (
                        <div
                          key={c._id}
                          className={`conversations-item-wrap ${
                            idConversationCurrent === c._id && "active"
                          } ${user._id === c.new && "newMess"}`}
                          onClick={() => hanleClickConversation(c)}
                        >
                          <ConversationsItem
                            conversation={c}
                            userId={user._id}
                          />
                          <span>Tin nhn mi</span>
                        </div>
                      ))}
                  </ul>
                </div>
                {conversationCurrent ? (
                  <div className="chat-concent">
                    <div className="chat-content-user">
                      <Link
                        to={`/profile/${infoFriend?.username}`}
                        className="chat-content-link"
                      >
                        <img
                          className="chat-content-user-img"
                          src={
                            infoFriend
                              ? `${apiUrl}/uploadfile/${infoFriend.avt}`
                              : ""
                          }
                          alt=""
                        />
                        <span className="chat-content-user-name">
                          {infoFriend && infoFriend.fullName}
                        </span>
                      </Link>
                    </div>
                    <div
                      className="chat-concent-messages"
                      ref={chatConcentMessages}
                    >
                      {currentChat?.map((m, i) =>
                        i === currentChat.length - 1 ? (
                          <Messages test={true} messages={m} key={i} />
                        ) : (
                          <Messages messages={m} key={i} />
                        )
                      )}
                    </div>
                    <div className="form-messages">
                      <TextareaAutosize
                        type="text"
                        name=""
                        id=""
                        className="form-messages-input"
                        maxRows={3}
                        minRows={2}
                        ref={formMessagesInputRef}
                        value={message}
                        onChange={handleChangeText}
                        spellCheck={false}
                      />

                      <button
                        onClick={handleSendMessage}
                        ref={buttonRef}
                        className="form-messages-btn o5active"
                      >
                        Send
                      </button>
                    </div>
                  </div>
                ) : (
                  <>
                    <div className="form-messages-no-messages">
                      <span className="form-messages-no-messages-icon"></span>
                      <h1
                        className="form-messages-no-messages-title o5active"
                        onClick={() => setModalCreate(true)}
                      >
                        To cuc tr chuyn mi{" "}
                      </h1>
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      {modalCreate && (
        <ModalCreateMessage
          handleCreateMessage={handleCreateMessage}
          setModalCreate={setModalCreate}
          userId={user._id}
        />
      )}
    </>
  );
};

export default Inbox;
