import { createContext, useState, useRef, useEffect } from "react";
import { io } from "socket.io-client";
import { useAuthContext } from "contexts";

export const HomeContext = createContext();

const HomeContextProvider = ({ children }) => {
  const [active, setActive] = useState("");
  const [statusMess, setStatusMess] = useState(false);
  const {
    authState: { user },
  } = useAuthContext();
  const socket = useRef();
  useEffect(() => {
    socket.current = io("https://sang-instagram-socket.herokuapp.com/");
    socket.current.emit("addUser", user?._id);
    socket.current.on("arrivalMessage", async () => {
      // setStatusMess(true);
      if (window.location.pathname !== "/inbox") {
        alert("C tin nhn");
      }
    });
  }, [user]);

  // useEffect(() => {
  //   if (statusMess) {
  //     alert("C tin nhn");
  //   }
  // }, [statusMess]);
  const HomeContextData = { active, setActive, socket };

  return (
    <HomeContext.Provider value={HomeContextData}>
      {children}
    </HomeContext.Provider>
  );
};

export default HomeContextProvider;
