import { useEffect, useRef, useState, useContext } from "react";
import { useHistory, useLocation } from "react-router-dom";
import clsx from "clsx";
import "./modal.css";
import { PostsContext } from "../../contexts/PostsContext";
import { AuthContext } from "../../contexts/AuthContext";
import { usePostsContext } from "contexts";
import Page from "../../view/page/Page";
import Spinner from "../spinner/Spinner";
import { apiUrl } from "../../contexts/constants";
import { useAuthContext } from "contexts";
import style from "./ModalCreateMessage.module.css";
import style1 from "./Modal.module.css";
import { format } from "timeago.js";

const ModalCreatePost = () => {
  // use hook
  const location = useLocation();
  const history = useHistory();
  const myRef = useRef(null);
  const preview = useRef(null);
  const inputRef = useRef(null);
  const bodyRef = useRef();
  const [edit, setEdit] = useState(false);
  const [alert, setAlert] = useState(false);
  const [spinner, setSpinner] = useState(false);
  const [description, setDescription] = useState("");
  const { addPost } = useContext(PostsContext);
  const {
    authState: { user },
  } = useContext(AuthContext);
  useEffect(() => {
    const goBack = (e) => {
      if (e.keyCode === 27) {
        if (myRef.current !== null) {
          myRef.current.click();
        }
      }
    };
    document.addEventListener("keyup", goBack);
    const handleChange = (e) => {
      const file = e.target.files[0];
      const avt = URL.createObjectURL(file);
      preview.current.src = avt;
      URL.revokeObjectURL(file);
      setEdit("Tip");
    };
    if (inputRef.current !== null) {
      inputRef.current.addEventListener("change", handleChange);
    }
    return () => {
      document.removeEventListener("keyup", goBack);
    };
  });
  //callback
  const handleAddText = async (e) => {
    if (edit === "Tip") {
      bodyRef.current.classList.add("add-edit");
      setEdit("Chia s");
    }
    if (edit === "Chia s") {
      const postForm = { file: inputRef.current.files[0], description };
      setSpinner(true);
      await addPost(postForm);
      setSpinner(false);
      history.push("/");
    }
  };
  const back = (e) => {
    if (edit) {
      setAlert(true);
    } else {
      e.stopPropagation();
      history.goBack();
    }
  };
  const handleCancel = (e) => {
    e.stopPropagation();
    history.goBack();
  };
  const handleChangeDescription = (e) => {
    setDescription(e.target.value);
  };

  return (
    <>
      <Page
        route="create"
        routePrev={
          location.state.prevPathName1.length > 1
            ? location.state.prevPathName1.slice(1)
            : location.state.prevPathName1
        }
      />
      <div className="modal">
        <div className="modal-overlay" onClick={back}></div>
        <div className="modal-body" ref={bodyRef}>
          {edit ? (
            <div className="modal-body-heard-active">
              <div style={{ cursor: "pointer" }} onClick={() => setAlert(true)}>
                <svg
                  color="#262626"
                  fill="#262626"
                  height={24}
                  viewBox="0 0 24 24"
                  width={24}
                >
                  <line
                    fill="none"
                    stroke="currentColor"
                    strokeLinecap="round"
                    strokeMiterlimit={10}
                    strokeWidth="1.5"
                    x1="1.23"
                    x2="23.233"
                    y1="12.004"
                    y2="12.004"
                  />
                  <polyline
                    fill="none"
                    points="8.919 3.817 0.733 12.004 8.919 20.19"
                    stroke="currentColor"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="1.5"
                  />
                </svg>
              </div>
              <span className="modal-body-heard-title">Chnh sa</span>
              <span className="modal-body-heard-next" onClick={handleAddText}>
                {edit}
              </span>
            </div>
          ) : (
            <div className="modal-body-heard">
              <h1 className="modal-create-post-title">To bi vit mi</h1>
              <hr
                style={{
                  borderColor: "rgb(204 204 204 / 37%",
                  borderWidth: "1px",
                }}
              />
            </div>
          )}
          <div className={`modal-body-content ${!edit ? "active" : ""}`}>
            <svg
              className="modal-create-post-img"
              color="#262626"
              fill="#262626"
              height={77}
              viewBox="0 0 97.6 77.3"
              width={96}
            >
              <path d="M16.3 24h.3c2.8-.2 4.9-2.6 4.8-5.4-.2-2.8-2.6-4.9-5.4-4.8s-4.9 2.6-4.8 5.4c.1 2.7 2.4 4.8 5.1 4.8zm-2.4-7.2c.5-.6 1.3-1 2.1-1h.2c1.7 0 3.1 1.4 3.1 3.1 0 1.7-1.4 3.1-3.1 3.1-1.7 0-3.1-1.4-3.1-3.1 0-.8.3-1.5.8-2.1z" />
              <path d="M84.7 18.4L58 16.9l-.2-3c-.3-5.7-5.2-10.1-11-9.8L12.9 6c-5.7.3-10.1 5.3-9.8 11L5 51v.8c.7 5.2 5.1 9.1 10.3 9.1h.6l21.7-1.2v.6c-.3 5.7 4 10.7 9.8 11l34 2h.6c5.5 0 10.1-4.3 10.4-9.8l2-34c.4-5.8-4-10.7-9.7-11.1zM7.2 10.8C8.7 9.1 10.8 8.1 13 8l34-1.9c4.6-.3 8.6 3.3 8.9 7.9l.2 2.8-5.3-.3c-5.7-.3-10.7 4-11 9.8l-.6 9.5-9.5 10.7c-.2.3-.6.4-1 .5-.4 0-.7-.1-1-.4l-7.8-7c-1.4-1.3-3.5-1.1-4.8.3L7 49 5.2 17c-.2-2.3.6-4.5 2-6.2zm8.7 48c-4.3.2-8.1-2.8-8.8-7.1l9.4-10.5c.2-.3.6-.4 1-.5.4 0 .7.1 1 .4l7.8 7c.7.6 1.6.9 2.5.9.9 0 1.7-.5 2.3-1.1l7.8-8.8-1.1 18.6-21.9 1.1zm76.5-29.5l-2 34c-.3 4.6-4.3 8.2-8.9 7.9l-34-2c-4.6-.3-8.2-4.3-7.9-8.9l2-34c.3-4.4 3.9-7.9 8.4-7.9h.5l34 2c4.7.3 8.2 4.3 7.9 8.9z" />
              <path d="M78.2 41.6L61.3 30.5c-2.1-1.4-4.9-.8-6.2 1.3-.4.7-.7 1.4-.7 2.2l-1.2 20.1c-.1 2.5 1.7 4.6 4.2 4.8h.3c.7 0 1.4-.2 2-.5l18-9c2.2-1.1 3.1-3.8 2-6-.4-.7-.9-1.3-1.5-1.8zm-1.4 6l-18 9c-.4.2-.8.3-1.3.3-.4 0-.9-.2-1.2-.4-.7-.5-1.2-1.3-1.1-2.2l1.2-20.1c.1-.9.6-1.7 1.4-2.1.8-.4 1.7-.3 2.5.1L77 43.3c1.2.8 1.5 2.3.7 3.4-.2.4-.5.7-.9.9z" />
            </svg>
            <label htmlFor="file-avt" className="modal-create-post-button">
              Chn t my tnh
            </label>
            <input
              type="file"
              id="file-avt"
              hidden
              accept=".png, .jpg, .gif"
              ref={inputRef}
            />
            <button className="modal-close" ref={myRef} onClick={back}>
              close
            </button>
          </div>
          <div className="wrap-form-post">
            <div className={`img-preview ${edit ? "active" : ""}`}>
              <img alt="avt" src="" ref={preview} />
            </div>
            <div className="form-post">
              <div className="user">
                <img
                  src={`${apiUrl}/uploadfile/${user.avt}`}
                  alt=""
                  className="user-img"
                />
                <span className="user-name">{user.fullName}</span>
              </div>
              <textarea
                name="description"
                id=""
                className="form-text"
                placeholder="Bn ang ngh g..."
                rows="10"
                value={description}
                spellCheck={false}
                onChange={handleChangeDescription}
              />
            </div>
            {spinner && <Spinner />}
          </div>
        </div>
        {alert && (
          <div className="modal children">
            <div className="modal-overlay"></div>
            <div className="modal-body">
              <div className="modal-body-cancel">
                <div className="modal-body-cancel-header">
                  <h1>B bi vit</h1>
                  <span>Nu ri i, bn s mt nhng g va chnh sa.</span>
                </div>
                <button className="cancel" onClick={handleCancel}>
                  B
                </button>
                <button
                  className="no-cancel"
                  onClick={() => {
                    setAlert(false);
                  }}
                >
                  Hy
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </>
  );
};

const ModalMenuPost = ({ setActiveFalse, post }) => {
  const {
    authState: { user },
  } = useAuthContext();

  const { updatePost } = useContext(PostsContext);
  const [update, setUpdate] = useState(false);
  const [cancelUpdate, setCancelUpdate] = useState(false);
  const [valueUpdate, setValueUpdate] = useState({
    description: post.description,
    file: null,
  });
  const [spinner, setSpinner] = useState(false);
  const { description } = valueUpdate;
  const refBody = useRef();
  const imgRef = useRef();
  const textRef = useRef();
  const { postDelete } = useContext(PostsContext);
  useEffect(() => {
    const handleClick = (e) => {
      if (refBody.current) {
        if (refBody.current.contains(e.target)) {
          if (!update) {
            setActiveFalse();
          }
          if (update) {
            setCancelUpdate(true);
          }
        }
      }
    };
    if (valueUpdate && textRef.current) {
      textRef.current.focus();
    }
    document.addEventListener("click", handleClick);
    return () => {
      document.removeEventListener("click", handleClick);
    };
  });

  const handleChange = (e) => {
    if (e.target.name === "file") {
      imgRef.current.src = URL.createObjectURL(e.target.files[0]);
      URL.revokeObjectURL(e.target.files[0]);
      setValueUpdate({ ...valueUpdate, [e.target.name]: e.target.files[0] });
      return;
    }
    setValueUpdate({ ...valueUpdate, [e.target.name]: e.target.value });
  };
  const handleUpdate = async () => {
    setSpinner(true);
    await updatePost(post, valueUpdate);
    setSpinner(false);
    setActiveFalse();
  };

  const body = update ? (
    <div className="update-post-container">
      <div className="update-post-header">
        <button
          className="update-post-btn"
          onClick={() => setCancelUpdate(true)}
        >
          Hy
        </button>
        <h1>Chnh sa bi vit </h1>

        <button className="update-post-btn" onClick={handleUpdate}>
          Xong
        </button>
      </div>
      <textarea
        type="text"
        value={description}
        name="description"
        onChange={handleChange}
        className="update-post-text"
        rows="4"
        spellCheck={false}
        ref={textRef}
      />
      <div className="update-post-form">
        <img
          className="update-post-img"
          src={`${apiUrl}/uploadFile/${post.file}`}
          alt=""
          ref={imgRef}
        />
        <div className="update-post-label">
          <label className="update-post-change-img" htmlFor="img-update">
            Thay i nh
          </label>
          <input
            type="file"
            hidden
            name="file"
            id="img-update"
            onChange={handleChange}
          />
        </div>
      </div>
      {spinner && <Spinner />}
      {cancelUpdate && (
        <div className="modal modal-post-children">
          <div className="modal-overlay"></div>
          <div className="modal-body">
            <div className="btn-cancel-edit">
              <h1 className="btn-cancel-title">
                Bn chc chc mun hy b chnh sa
              </h1>
              <div className="displayFlex justifySpaceAround">
                <button
                  className="btn-item o5active"
                  onClick={() => {
                    setUpdate(false);
                    setCancelUpdate(false);
                  }}
                >
                  ng 
                </button>
                <button
                  className="btn-item o5active"
                  onClick={() => setCancelUpdate(false)}
                >
                  Hy
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  ) : (
    <>
      <ul className="modal-menu-list">
        {user?._id === post.user?._id ? (
          <li
            className="modal-menu-item"
            onClick={() => {
              postDelete(post);
              setActiveFalse();
            }}
          >
            Xa bi vit
          </li>
        ) : (
          <li className="modal-menu-item disabled">
            Xa bi vit(Khng phi ch tus)
          </li>
        )}
        {user?._id === post.user?._id ? (
          <li className="modal-menu-item" onClick={() => setUpdate(true)}>
            Chnh sa bi vit
          </li>
        ) : (
          <li className="modal-menu-item disabled">
            Chnh sa bi vit(Khng phi ch tus)
          </li>
        )}

        <li className="modal-menu-item" onClick={setActiveFalse}>
          Hy
        </li>
      </ul>
    </>
  );

  return (
    <div className="modal">
      <div className="modal-overlay" ref={refBody}></div>
      <div className="modal-body">{body}</div>
    </div>
  );
};
const ModalCreateMessage = ({
  userId,
  handleCreateMessage,
  setModalCreate,
}) => {
  const [frs, setFrs] = useState(null);
  const [value, setValue] = useState("");
  const { searchFullName } = useContext(AuthContext);
  const handleChangeInput = async (e) => {
    setValue(e.target.value);
    if (e.target.value !== "") {
      const res = await searchFullName(value);
      if (res.success) {
        const arrayFr = res.user.filter((u) => u._id !== userId);
        setFrs(arrayFr);
      }
    } else {
      setFrs(null);
    }
  };
  return (
    <div className="modal">
      <div className="modal-overlay"></div>
      <div className="modal-body">
        <div className={style.createMessages}>
          <h2 className="create-message-title">Tin nhn mi</h2>
          <input
            type="text"
            placeholder="Tm kim bn b"
            className={style.inputText}
            value={value}
            onChange={handleChangeInput}
          />
          <div
            onClick={() => setModalCreate(false)}
            className={clsx(style.iconCloseCreateMessage, "o5active")}
          >
            <i className="far fa-times-circle"></i>
          </div>
          {frs !== null && (
            <ul className={style.listfr}>
              {frs.map((f, i) => (
                <li
                  key={i}
                  onClick={() => handleCreateMessage(f._id)}
                  className={style.itemFr}
                >
                  <img
                    src={`${apiUrl}/uploadFile/${f.avt}`}
                    className={style.itemImg}
                    alt="xxx"
                  />
                  <h3 className={style.itemName}>{f.fullName}</h3>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
};

const ModalPostDetail = ({ data, setModal }) => {
  const {
    authState: { user },
  } = useAuthContext();
  const history = useHistory();
  const { likePost, commentPost } = usePostsContext();
  const [like, setLike] = useState({
    status: data.likes.includes(user._id),
    numberLike: data.likes.length,
  });
  const [comment, setComment] = useState("");
  const [comments, setComments] = useState(
    data.comments.sort((b, a) => a.createdAt - b.createdAt)
  );
  const handleToProfile = () => {
    history.push(`/profile/${data.user.username}`);
  };
  const handleLikePost = async () => {
    const res = await likePost(data._id);
    if (res.message === "add") {
      setLike({ ...like, status: true, numberLike: like.numberLike + 1 });
    }
    if (res.message === "delete") {
      setLike({ ...like, status: false, numberLike: like.numberLike - 1 });
    }
  };
  const handleComment = async (e) => {
    if (e.keyCode === 13) {
      if (comment) {
        const res = await commentPost({
          postId: data._id,
          userComment: {
            fullName: data.user.fullName,
            username: data.user.username,
            avt: data.user.avt,
            textCommnet: comment,
            createdAt: Date.now(),
          },
        });
        if (res.success) {
          setComment("");
          setComments(res.comment.sort((b, a) => a.createdAt - b.createdAt));
        }
      }
    }
  };
  const handleComment2 = async () => {
    if (comment) {
      const res = await commentPost({
        postId: data._id,
        userComment: {
          fullName: data.user.fullName,
          username: data.user.username,
          avt: data.user.avt,
          textCommnet: comment,
          createdAt: Date.now(),
        },
      });
      if (res.success) {
        setComment("");
        setComments(res.comment.sort((b, a) => a.createdAt - b.createdAt));
      }
    }
  };
  console.log(comments);
  return (
    <>
      <div className="modal">
        <div className="modal-overlay"></div>
        <div className="modal-body post-detail">
          <div className={style1.imgPost}>
            <img src={`${apiUrl}/uploadFile/${data.file}`} alt="" />
          </div>
          <div className={style1.infoPost}>
            <div className={style1.infoUser}>
              <img
                src={`${apiUrl}/uploadFile/${data.user.avt}`}
                alt=""
                onClick={handleToProfile}
              />
              <div>
                <span className={style1.username} onClick={handleToProfile}>
                  {data.user.fullName}
                </span>
                <div className={style1.timeUpload}>
                  {format(data.createdAt)}
                </div>
              </div>
            </div>
            <div className={style1.contentPost}>{data.description}</div>
            <div className={style1.boxStatus}>
              <span className={style1.boxStatus1}>
                {like.numberLike} lt thch
              </span>
              <span className={style1.boxStatus2}>
                {comments.length} bnh lun
              </span>
            </div>
            <div className={style1.formComment}>
              <input
                type="text"
                placeholder="Vit bnh lun"
                value={comment}
                onKeyUp={handleComment}
                onChange={(e) => setComment(e.target.value)}
              />
            </div>
            <div className={style1.boxButton}>
              <button
                className={clsx(style1.btnLike, {
                  [style1.likeActive]: like.status,
                })}
                onClick={handleLikePost}
              >
                Thch
              </button>
              <button
                className={style1.btnLike}
                onClick={() => {
                  handleComment2();
                }}
              >
                Bnh lun
              </button>
            </div>
            <div className={style1.boxComment}>
              {comments?.map((item, index) => (
                <div key={index} className={style1.commentItem}>
                  <div className={style1.avtUserComment}>
                    <img src={`${apiUrl}/uploadFile/${item.avt}`} alt="" />
                  </div>
                  <div className={style1.infoUserComment}>
                    <span className={style1.nameUserComment}>
                      {item.fullName}
                    </span>
                    <p className={style1.textComment}>{item.textCommnet}</p>
                    <span className={style1.createAtCm}>
                      {format(item.createdAt)}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div
            className={clsx(style1.iconCancel, "o5active")}
            onClick={() => {
              setModal(false);
            }}
          >
            <i className="far fa-times-circle"></i>
          </div>
        </div>
      </div>
    </>
  );
};
export { ModalCreatePost, ModalMenuPost, ModalCreateMessage, ModalPostDetail };
