import "../login/authForm.css";
import { Link } from "react-router-dom";
import { useState, useContext, useEffect, useRef } from "react";
import { AuthContext } from "../../contexts/AuthContext";
import Alert from "../alert/Alert";

const Register = () => {
  const myRef = useRef();
  const { register } = useContext(AuthContext);
  const [alert, setAlert] = useState(null);

  const [value, setValue] = useState({
    username: "",
    password: "",
    confirmPassword: "",
    fullName: "",
  });
  const { username, password, confirmPassword, fullName } = value;
  const handleChange = (e) => {
    setValue({
      ...value,
      [e.target.name]: e.target.value,
    });
  };
  const handleRegister = async (value) => {
    const registerData = await register(value);
    if (!registerData.success) {
      setAlert(registerData.message);
    }
  };
  useEffect(() => {
    const handleClick = (e) => {
      if (e.keyCode === 13) {
        myRef.current.click();
      }
    };
    document.addEventListener("keyup", handleClick);
    return () => {
      document.removeEventListener("keyup", handleClick);
    };
  }, []);
  return (
    <div className="wrap-form">
      <div className="auth-form">
        <div className="logo-form"></div>
        <input
          type="text"
          name="username"
          className="input-form"
          placeholder="Tn ti khon"
          onChange={handleChange}
          value={username}
        />
        <input
          type="text"
          name="fullName"
          className="input-form"
          placeholder="Tn ngi dng"
          onChange={handleChange}
          value={fullName}
        />
        <input
          type="password"
          className="input-form"
          name="password"
          placeholder="Mt khu"
          onChange={handleChange}
          value={password}
        />

        <input
          type="password"
          className="input-form"
          name="confirmPassword"
          placeholder="Nhp li mt khu"
          onChange={handleChange}
          value={confirmPassword}
        />
        <div>
          {alert && <Alert info={alert} />}
          {/* add class disabled to disabled */}
          <button
            ref={myRef}
            className="form-button"
            onClick={() => handleRegister(value)}
          >
            ng k
          </button>
          <div className="wrap-switch">
            <p className="switch-text">Bn  c ti khon ?</p>
            <Link to="/login" className="switch-link">
              ng nhp
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
