import {
  ADD_POST,
  LOAD_POST_FAILED,
  LOAD_POST_SUCCESS,
  DELETE_POST,
  UPDATE_POST,
  LOAD_ALL_POST_SUCCESS,
  LOAD_ALL_POST_FAILED,
  RELOAD_POST,
} from "../contexts/constants";
export const postsReducer = (state, actions) => {
  const { type, payload } = actions;
  switch (type) {
    case RELOAD_POST:
      return {
        ...state,
        postLoading: true,
      };
    case ADD_POST:
      return {
        ...state,
        posts: [payload, ...state.posts],
        postLoading: false,
      };
    case LOAD_POST_SUCCESS:
      return {
        ...state,
        posts: payload,
        postLoading: false,
      };
    case LOAD_ALL_POST_SUCCESS:
      return {
        ...state,
        postsAll: payload,
        postAllLoading: false,
      };
    case LOAD_ALL_POST_FAILED:
      return {
        ...state,
        postsAll: [],
        postAllLoading: false,
      };
    case UPDATE_POST:
      return {
        ...state,
        posts: state.posts.map((post) =>
          post._id === payload._id ? payload : post
        ),
        postLoading: false,
      };
    case LOAD_POST_FAILED:
      return {
        ...state,
        posts: [],
        postLoading: false,
      };
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter((post) => post._id !== payload._id),
      };

    default:
      return state;
  }
};
