import { createContext, useReducer } from "react";
import axios from "axios";
import {
  apiUrl,
  ADD_POST,
  LOAD_POST_FAILED,
  LOAD_POST_SUCCESS,
  DELETE_POST,
  UPDATE_POST,
  LOAD_ALL_POST_SUCCESS,
  LOAD_ALL_POST_FAILED,
} from "./constants";
import { postsReducer } from "../reducers/postsReducer";
import uploadFile from "../utils/uploadFile";
import deleteFile from "../utils/deleteFile";

export const PostsContext = createContext();

const PostsContextProvider = ({ children }) => {
  const [postState, dispatch] = useReducer(postsReducer, {
    post: null,
    posts: [],
    postLoading: true,
    postsAll: [],
    postAllLoading: true,
  });

  const { posts } = postState;
  const addPost = async (post) => {
    const { file } = post;

    try {
      if (file) {
        const newFile = await uploadFile(file);
        post.file = newFile;
      }
      const response = await axios.post(`${apiUrl}/posts/create`, post);
      if (response.data.success) {
        dispatch({ type: ADD_POST, payload: response.data.post });
      }
      return response.data;
    } catch (error) {
      return { message: "add post failes", success: false };
    }
  };
  const getPosts = async () => {
    try {
      const response = await axios.get(`${apiUrl}/posts`);
      if (response.data.success) {
        dispatch({ type: LOAD_POST_SUCCESS, payload: response.data.posts });
      }
    } catch (error) {
      dispatch({ type: LOAD_POST_FAILED });
    }
  };
  const getAllPosts = async () => {
    try {
      const response = await axios.get(`${apiUrl}/posts/all`);
      if (response.data.success) {
        dispatch({ type: LOAD_ALL_POST_SUCCESS, payload: response.data.posts });
      }
    } catch (error) {
      dispatch({ type: LOAD_ALL_POST_FAILED });
    }
  };

  const postDelete = async (post) => {
    try {
      deleteFile(post.file);
      const response = await axios.delete(`${apiUrl}/posts/${post._id}`);
      if (response.data.success) {
        dispatch({ type: DELETE_POST, payload: response.data.post });
      }
      return response.data;
    } catch (error) {
      return { success: false, message: "Can not delete!" };
    }
  };
  const updatePost = async (post, newPost) => {
    const { file } = newPost;

    try {
      if (file) {
        const newFile = await uploadFile(file);
        deleteFile(post.file);
        newPost.file = newFile;
      }
      if (file === null) {
        newPost.file = post.file;
      }
      const response = await axios.put(`${apiUrl}/posts/${post._id}`, newPost);
      if (response.data.success) {
        dispatch({ type: UPDATE_POST, payload: response.data.post });
      }
    } catch (error) {
      return error;
    }
  };
  const likePost = async (postId) => {
    try {
      const res = await axios.post(`${apiUrl}/posts/like`, { postId });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      console.log(error.message);
    }
  };
  const commentPost = async (infoComment) => {
    try {
      const res = await axios.post(`${apiUrl}/posts/comment`, {
        postId: infoComment.postId,
        infoUserComment: infoComment.userComment,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      console.log(error.message);
    }
  };
  const getPostById = async (userID) => {
    try {
      const res = await axios.get(`${apiUrl}/posts/${userID}`);
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  const PostsContextData = {
    postState,
    addPost,
    getPostById,
    postDelete,
    updatePost,
    likePost,
    getPosts,
    commentPost,
    getAllPosts,
    dispatch,
  };
  return (
    <PostsContext.Provider value={PostsContextData}>
      {children}
    </PostsContext.Provider>
  );
};
export default PostsContextProvider;
