import "./messages.css";
import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../contexts/AuthContext";
import axios from "axios";
import { apiUrl } from "../../contexts/constants";
const Messages = ({ messages, test }) => {
  const {
    authState: { user },
  } = useContext(AuthContext);
  const [userFr, setUserFr] = useState();
  useEffect(() => {
    const getUser = async () => {
      if (messages.senderId !== user._id) {
        const user = await axios.get(`${apiUrl}/user?id=${messages.senderId}`);
        if (user.data.success) {
          setUserFr(user.data.user);
        }
      }
    };
    getUser();
    if (test) {
      const lastMessage =
        document.querySelector(
          ".chat-concent-messages-item-send.last-message"
        ) ||
        document.querySelector(".chat-concent-messages-item-get.last-message");
      lastMessage.scrollIntoView({
        behavior: "smooth",
        block: "end",
        inline: "nearest",
      });
    }
  }, [user._id, messages]);

  return (
    <>
      {messages.senderId === user._id ? (
        <div
          className={`chat-concent-messages-item-send ${
            test ? "last-message" : ""
          }`}
        >
          <div className="chat-concent-messages-item-send-content">
            <div className="chat-concent-messages-item-img-wrap">
              <img
                className="chat-concent-messages-item-img"
                src={`${apiUrl}/uploadfile/${user.avt}`}
                alt=""
              />
            </div>
            <span className="chat-concent-messages-item-text">
              {messages.text}
            </span>
          </div>
        </div>
      ) : (
        <div
          className={`chat-concent-messages-item-get ${
            test ? "last-message" : ""
          }`}
        >
          <div className="chat-concent-messages-item-img-wrap">
            <img
              className="chat-concent-messages-item-img"
              src={userFr && `${apiUrl}/uploadfile/${userFr?.avt}`}
              alt=""
            />
          </div>
          <span className="chat-concent-messages-item-text">
            {messages.text}
          </span>
        </div>
      )}
    </>
  );
};

export default Messages;
