import { useEffect, useState } from "react";
import "./conversationsItem.css";
import axios from "axios";
import { apiUrl } from "../../contexts/constants";

const ConversationsItem = ({ conversation, userId }) => {
  const userFr = conversation.members.find((m) => m !== userId);
  const [user, setUser] = useState();
  useEffect(() => {
    const getUser = async () => {
      const res = await axios.get(`${apiUrl}/user?id=${userFr}`);
      setUser(res.data.user);
    };
    getUser();
  }, [userFr]);
  return (
    <li className={`conversations-item`}>
      {user && (
        <>
          <img
            src={user ? `${apiUrl}/uploadfile/${user?.avt}` : ""}
            alt=""
            className="conversations-item-img"
          />
          <div className="conversations-item-content">
            <div className="conversations-item-name">{user?.fullName}</div>
          </div>
        </>
      )}
    </li>
  );
};

export default ConversationsItem;
