import { createContext, useReducer, useEffect } from "react";
import { authReducer } from "../reducers/authReducer";
import axios from "axios";
import { LOCAL_STORAGE_TOKEN_NAME, apiUrl, SET_AUTH } from "./constants";
import setAuthToken from "../utils/setAuthToken";
import uploadFile from "../utils/uploadFile";
import deleteFile from "../utils/deleteFile";

export const AuthContext = createContext();

const AuthContextProvider = ({ children }) => {
  const [authState, dispatch] = useReducer(authReducer, {
    isAuthenticated: false,
    authLoading: true,
    user: null,
  });
  // load user
  const loadUser = async () => {
    if (localStorage[LOCAL_STORAGE_TOKEN_NAME]) {
      setAuthToken(localStorage[LOCAL_STORAGE_TOKEN_NAME]);
    }
    try {
      const response = await axios.get(`${apiUrl}/auth/`);

      if (response.data.success) {
        dispatch({
          type: SET_AUTH,
          payload: {
            user: response.data.user,
            isAuthenticated: true,
          },
        });
      }
    } catch (error) {
      localStorage.removeItem(LOCAL_STORAGE_TOKEN_NAME);
      setAuthToken(null);
      dispatch({
        type: SET_AUTH,
        payload: {
          user: null,
          isAuthenticated: false,
        },
      });
    }
  };

  useEffect(() => loadUser(), []);

  //login
  const login = async (loginForm) => {
    try {
      const response = await axios.post(`${apiUrl}/auth/login`, loginForm);
      if (response.data.success) {
        localStorage.setItem(
          LOCAL_STORAGE_TOKEN_NAME,
          response.data.accessToken
        );
      }
      // document.location.reload();
      await loadUser();
      return response.data;
    } catch (error) {
      if (error.response.data) return error.response.data;
      else return { success: false, message: "Error" };
    }
  };

  //log out
  const logOut = () => {
    localStorage.removeItem(LOCAL_STORAGE_TOKEN_NAME);
    setAuthToken(null);
    document.location.reload();
    dispatch({
      type: "SET_AUTH",
      payload: {
        user: null,
        isAuthenticated: false,
      },
    });
  };
  //register
  const register = async (registerForm) => {
    try {
      const response = await axios.post(
        `${apiUrl}/auth/register`,
        registerForm
      );
      if (response.data.success) {
        localStorage.setItem(
          LOCAL_STORAGE_TOKEN_NAME,
          response.data.accessToken
        );
      }
      await loadUser();

      return response.data;
    } catch (error) {
      if (error.response.data) return error.response.data;
      else return { message: error.message, success: false };
    }
  };
  // uploadAvt
  const updatedAvt = async (file, user) => {
    try {
      if (user.avt === "avt-default.jpg") {
        const avtNew = await uploadFile(file);
        const response = await axios.put(`${apiUrl}/auth/updateAvt`, {
          avt: avtNew,
        });
        if (response.data.success) {
          await loadUser();
        }
        return;
      }
      if (user.avt !== "avt-default.jpg") {
        deleteFile(user.avt);
        const avtNew = await uploadFile(file);
        const response = await axios.put(`${apiUrl}/auth/updateAvt`, {
          avt: avtNew,
        });
        if (response.data.success) {
          await loadUser();
        }
        return;
      }
    } catch (error) {
      return error;
    }
  };
  //search user
  const searchFullName = async (value) => {
    try {
      const res = await axios.post(`${apiUrl}/auth/search`, {
        value,
      });
      return res.data;
    } catch (error) {
      return error;
    }
  };
  //get user fr
  const getUserFr = async (username) => {
    try {
      const res = await axios.get(`${apiUrl}/auth/${username}`);
      return res.data;
    } catch (error) {
      return error;
    }
  };
  // add friend
  const addFriend = async (friendId) => {
    try {
      const res = await axios.post(`${apiUrl}/auth/addfriend`, { friendId });
      if (res.data.success) {
        dispatch({
          type: SET_AUTH,
          payload: { user: res.data.user, isAuthenticated: true },
        });
      }
      return res.data;
    } catch (error) {
      return { success: false };
    }
  };
  // delete friend
  const deleteFriend = async (friendId) => {
    try {
      const res = await axios.post(`${apiUrl}/auth/deletefriend`, { friendId });
      if (res.data.success) {
        dispatch({
          type: SET_AUTH,
          payload: { user: res.data.user, isAuthenticated: true },
        });
      }
      return res.data;
    } catch (error) {
      return { success: false };
    }
  };
  // add follow
  const addFollow = async (username) => {
    await axios.post(`${apiUrl}/auth/addfollow`, { username });
  };
  const deleteFollow = async (username) => {
    await axios.post(`${apiUrl}/auth/deletefollow`, { username });
  };
  const AuthContextData = {
    authState,
    login,
    register,
    logOut,
    updatedAvt,
    searchFullName,
    getUserFr,
    addFriend,
    deleteFriend,
    addFollow,
    deleteFollow,
  };
  return (
    <AuthContext.Provider value={AuthContextData}>
      {children}
    </AuthContext.Provider>
  );
};
export default AuthContextProvider;
