import style from "./PostLayoutItem.module.css";
import { useEffect, useRef, useState } from "react";
import { apiUrl } from "contexts/constants";
import clsx from "clsx";
import Spinner from "components/spinner/Spinner";
import { ModalPostDetail } from "components";

const PostLayoutItem = ({ data, index, last, them, toggleThem }) => {
  const [body, setBody] = useState();
  const [modal, setModal] = useState(false);
  const handleClickPost = (dataPost) => {
    setModal(dataPost);
  };
  useEffect(() => {
    if (
      (!last && index % 3 === 0) ||
      (last && index % 3 === 0 && data.length === 3) ||
      (!last && index === 0)
    ) {
      setBody(
        <div className={clsx(style.root, { [style.reverse]: them.current })}>
          <div className={style.img_1}>
            <div
              className={style.img_1_item}
              style={{
                backgroundImage: `url(${apiUrl}/uploadfile/${data[0].file})`,
              }}
              onClick={() => handleClickPost(data[0])}
            ></div>
          </div>
          <div className={style.img_2_3}>
            <div className={style.img_2}>
              <div
                className={style.img_2_item}
                style={{
                  backgroundImage: `url(${apiUrl}/uploadfile/${data[1].file})`,
                }}
                onClick={() => handleClickPost(data[1])}
              ></div>
            </div>
            <div className={style.img_3}>
              <div
                className={style.img_3_item}
                style={{
                  backgroundImage: `url(${apiUrl}/uploadfile/${data[2].file})`,
                }}
                onClick={() => handleClickPost(data[2])}
              ></div>
            </div>
          </div>
        </div>
      );
      toggleThem();
    } else {
      setBody(
        <div className={style.root}>
          {data.map((d, i) => (
            <div key={i} className={style.post}>
              <div
                className={style.postImg}
                style={{
                  backgroundImage: `url(${apiUrl}/uploadfile/${d.file})`,
                }}
                onClick={() => handleClickPost(d)}
              ></div>
            </div>
          ))}
        </div>
      );
    }
  }, []);

  return body ? (
    <>
      {body}
      {modal && <ModalPostDetail setModal={setModal} data={modal} />}
    </>
  ) : (
    <Spinner />
  );
};

export default PostLayoutItem;
