import Header from "../../components/header/Header";
import Explore from "../../components/explore/Explore";
import Home from "../../components/home/Home";
import Inbox from "../../components/inbox/Inbox";
import Profile from "../../components/profile/Profile";
import { useContext, useEffect, useRef, memo } from "react";
import { HomeContext } from "../../contexts/HomeContext";
import { useLocation, useParams } from "react-router-dom";
const Page = ({ route, routePrev }) => {
  const { setActive } = useContext(HomeContext);
  const location = useLocation();
  const checkNumber = useRef(0);
  const checkLink = useRef("");
  checkNumber.current = checkNumber.current + 1;
  const { user, id } = useParams();

  let body;
  useEffect(() => {
    setActive(route);
  });
  if (location.state !== undefined) {
    if (location.state.prevPathName2) {
      if (checkNumber.current <= 2) {
        checkLink.current = location.state.prevPathName2;
        body = (
          <>
            <Header />
            {location.state.prevPathName2 === "/" && <Home />}
            {location.state.prevPathName2 === "/inbox" && <Inbox />}
            {location.state.prevPathName2 === "/explore" && <Explore />}
          </>
        );
      } else {
        body = (
          <>
            <Header />

            {(checkLink.current === "/" ||
              checkLink.current === "/nofification") && <Home />}
            {checkLink.current === "/inbox" && <Inbox />}
            {checkLink.current === "/explore" && <Explore />}
          </>
        );
      }
    }
  }
  if (routePrev && typeof routePrev === "string") {
    body = (
      <>
        <Header />
        {routePrev === "/" && <Home />}
        {routePrev === "inbox" && <Inbox />}
        {routePrev === "explore" && <Explore />}
      </>
    );
  }
  if (!routePrev && !location.state) {
    body = (
      <>
        <Header />
        {route === "home" && <Home />}
        {route === "inbox" && <Inbox id={id} />}
        {route === "explore" && <Explore />}
        {!route && <Profile username={user} />}
      </>
    );
  }
  return body;
};

export default memo(Page);
