import "./postItem.css";
import { apiUrl } from "contexts/constants";
import { useState, useContext } from "react";
import { ModalMenuPost } from "../modals/Modal";
import { PostsContext } from "contexts/PostsContext";
import { AuthContext } from "contexts/AuthContext";
import { format } from "timeago.js";
import { Link } from "react-router-dom";
import { usePostsContext } from "contexts";

const PostItem = ({ post }) => {
  const {
    authState: { user },
  } = useContext(AuthContext);
  const { commentPost } = usePostsContext();
  const [active, setActive] = useState(false);
  const [actionComment, setActionComment] = useState(false);
  const [valueComment, setValueComment] = useState("");
  const [comments, setComments] = useState(
    post.comments.sort((a, b) => b.createdAt - a.createdAt)
  );
  const [like, setLike] = useState({
    numberLike: post.likes.length,
    status: post.likes.includes(user._id),
  });
  const { likePost } = useContext(PostsContext);

  const handleLikePost = async () => {
    const res = await likePost(post._id);
    if (res.message === "add") {
      setLike({ ...like, status: true, numberLike: like.numberLike + 1 });
    }
    if (res.message === "delete") {
      setLike({ ...like, status: false, numberLike: like.numberLike - 1 });
    }
  };
  const handleComment = async (e) => {
    if (e.keyCode === 13) {
      if (valueComment) {
        const res = await commentPost({
          postId: post._id,
          userComment: {
            fullName: user.fullName,
            username: user.username,
            avt: user.avt,
            textCommnet: valueComment,
            createdAt: Date.now(),
          },
        });
        if (res.success) {
          setValueComment("");
          setComments(res.comment.sort((b, a) => a.createdAt - b.createdAt));
          setActionComment(true);
        }
      }
    }
  };
  return (
    <div className="post-item">
      <div className="post-item-wrap-info-caption">
        <div className="post-item-user">
          <div className="post-item-user-body">
            <Link
              className="post-item-user-link"
              to={`/profile/${post.user.username}`}
            >
              <img
                src={`${apiUrl}/uploadfile/${post.user.avt}`}
                alt=""
                className="post-item-user-avt"
              />
            </Link>
            <div className="post-item-user-info">
              <Link
                className="post-item-user-link"
                to={`/profile/${post.user.username}`}
              >
                <span className="post-item-user-name">
                  {post.user.fullName}
                </span>
              </Link>
              <span className="post-item-user-timeUpdatePost">
                {format(post.createdAt)}
              </span>
            </div>
          </div>
          <div className="post-item-icon-select">
            <svg
              color="#262626"
              fill="#262626"
              height="24"
              role="img"
              viewBox="0 0 24 24"
              width="24"
              onClick={() => setActive(true)}
            >
              <circle cx="12" cy="12" r="1.5"></circle>
              <circle cx="6.5" cy="12" r="1.5"></circle>
              <circle cx="17.5" cy="12" r="1.5"></circle>
            </svg>
            {active && (
              <ModalMenuPost
                setActiveFalse={() => setActive(false)}
                post={post}
              />
            )}
          </div>
        </div>
        <p className="post-item-caption">{post.description}</p>
      </div>

      <img
        alt="post-img"
        className="post-item-img"
        src={`${apiUrl}/uploadfile/${post.file}`}
      ></img>
      <div className="post-item-action">
        <div className="post-item-action-like" onClick={handleLikePost}>
          {like.status ? (
            <svg
              color="#ed4956"
              fill="#ed4956"
              height={26}
              role="img"
              viewBox="0 0 48 48"
              width={26}
            >
              <path d="M34.6 3.1c-4.5 0-7.9 1.8-10.6 5.6-2.7-3.7-6.1-5.5-10.6-5.5C6 3.1 0 9.6 0 17.6c0 7.3 5.4 12 10.6 16.5.6.5 1.3 1.1 1.9 1.7l2.3 2c4.4 3.9 6.6 5.9 7.6 6.5.5.3 1.1.5 1.6.5s1.1-.2 1.6-.5c1-.6 2.8-2.2 7.8-6.8l2-1.8c.7-.6 1.3-1.2 2-1.7C42.7 29.6 48 25 48 17.6c0-8-6-14.5-13.4-14.5z" />
            </svg>
          ) : (
            <svg
              color="#262626"
              fill="#262626"
              height={26}
              role="img"
              viewBox="0 0 48 48"
              width={26}
            >
              <path d="M34.6 6.1c5.7 0 10.4 5.2 10.4 11.5 0 6.8-5.9 11-11.5 16S25 41.3 24 41.9c-1.1-.7-4.7-4-9.5-8.3-5.7-5-11.5-9.2-11.5-16C3 11.3 7.7 6.1 13.4 6.1c4.2 0 6.5 2 8.1 4.3 1.9 2.6 2.2 3.9 2.5 3.9.3 0 .6-1.3 2.5-3.9 1.6-2.3 3.9-4.3 8.1-4.3m0-3c-4.5 0-7.9 1.8-10.6 5.6-2.7-3.7-6.1-5.5-10.6-5.5C6 3.1 0 9.6 0 17.6c0 7.3 5.4 12 10.6 16.5.6.5 1.3 1.1 1.9 1.7l2.3 2c4.4 3.9 6.6 5.9 7.6 6.5.5.3 1.1.5 1.6.5.6 0 1.1-.2 1.6-.5 1-.6 2.8-2.2 7.8-6.8l2-1.8c.7-.6 1.3-1.2 2-1.7C42.7 29.6 48 25 48 17.6c0-8-6-14.5-13.4-14.5z" />
            </svg>
          )}
        </div>
        <div
          className="post-item-action-comment"
          onClick={() => setActionComment(!actionComment)}
        >
          <svg
            color="#262626"
            fill="#262626"
            height={24}
            role="img"
            viewBox="0 0 48 48"
            width={24}
          >
            <path
              clipRule="evenodd"
              d="M47.5 46.1l-2.8-11c1.8-3.3 2.8-7.1 2.8-11.1C47.5 11 37 .5 24 .5S.5 11 .5 24 11 47.5 24 47.5c4 0 7.8-1 11.1-2.8l11 2.8c.8.2 1.6-.6 1.4-1.4zm-3-22.1c0 4-1 7-2.6 10-.2.4-.3.9-.2 1.4l2.1 8.4-8.3-2.1c-.5-.1-1-.1-1.4.2-1.8 1-5.2 2.6-10 2.6-11.4 0-20.6-9.2-20.6-20.5S12.7 3.5 24 3.5 44.5 12.7 44.5 24z"
              fillRule="evenodd"
            />
          </svg>
        </div>
      </div>
      <div className="post-number-like-comment">
        <h2 className="post-item-action-likeNumber">
          {like.numberLike} lt thch
        </h2>
        <h2
          className="post-item-action-cmtNumber o5active"
          onClick={() => setActionComment(!actionComment)}
        >
          {comments.length} bnh lun
        </h2>
      </div>

      <div className="form-comment">
        <input
          type="text"
          className="form-input-comment"
          placeholder="Vit bnh lun"
          value={valueComment}
          onChange={(e) => setValueComment(e.target.value)}
          onKeyUp={handleComment}
        />
        {actionComment && (
          <ul className="list-comment">
            {comments.map((c, i) => (
              <li key={i} className="item-comment">
                <div className="item-img-user-comment">
                  <Link
                    className="item-name-user-link-info"
                    to={`/profile/${c.username}`}
                  >
                    <img src={`${apiUrl}/uploadfile/${c.avt}`} alt="" />{" "}
                  </Link>
                </div>
                <div className="item-info-user-commnet">
                  <Link
                    className="item-name-user-link-info"
                    to={`/profile/${c.username}`}
                  >
                    <h3 className="item-name-user-comment">{c.fullName}</h3>
                  </Link>
                  <p className="item-text-comment">{c.textCommnet}</p>
                  <span className="item-text-timeComment">
                    {format(c.createdAt)}
                  </span>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default PostItem;
