import "./notification.css";
import { useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
const Notification = () => {
  const history = useHistory();
  const myRef = useRef(null);
  const handleBack = (e) => {
    if (e.keyCode === 27) {
      history.goBack();
    }
  };
  useEffect(() => {
    const handleClick = (e) => {
      if (myRef.current !== null) {
        if (!myRef.current.contains(e.target)) {
          history.goBack();
        }
      }
    };

    document.addEventListener("keyup", handleBack);

    document.addEventListener("click", handleClick);
    return () => {
      document.removeEventListener("keyup", handleBack);
      document.removeEventListener("click", handleClick);
    };
  });
  return (
    <div className="notification-header-wrap">
      <div className="notification-header" ref={myRef}>
        <h2 className="notification-title">Tt c thng bo</h2>
        <ul className="notification-list">
          <li className="notification-item">
            <h1>(Chc nng ang pht trin. Xin cm n)</h1>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Notification;
