import React from "react";
import "./searchHistory.css";
import { Link } from "react-router-dom";
import { apiUrl } from "../../contexts/constants";

const SearchHistory = ({ user }) => {
  let body;
  if (user && user.length > 0) {
    body = (
      <ul className="list-user-search">
        {user.map((user) => (
          <li key={user._id} className="list-user-item">
            <Link
              to={`/profile/${user.username}`}
              className="list-user-item-link"
            >
              <img
                className="list-user-item-img"
                src={`${apiUrl}/uploadfile/${user.avt}`}
                alt=""
              />
              <h1 className="list-user-item-name">{user.fullName}</h1>
            </Link>
          </li>
        ))}
      </ul>
    );
  } else {
    body = (
      <div className="header-search-history-content">
        <p className="header-search-history-text">
          Khng c ni dung tm kim mi y
        </p>
      </div>
    );
  }
  return (
    <div className="header-search-history">
      <div className="header-search-history-tg"></div>
      <h1 className="header-search-history-title">Gn y</h1>
      {body}
    </div>
  );
};

export default SearchHistory;
